
<style scoped>
.login-box {
  max-width: 300px;
  margin-left: auto;
  margin-right: auto;
}

.login-btn {
  width: 100%;
  margin-bottom: 50px;
  margin-top: 50px;
}

.input-box {
  margin-bottom: 5px;
  margin-top: 5px;
}

.title {
  color: gray;
  display: inline-block;
  padding-right: 20px;
}
</style>


<template>
  <div class="login-box">
    <div>
      <h1 class="title">登录</h1>
      <el-button type="text" @click="signUp">注册</el-button>
      <el-button type="text" @click="forgetPassword">忘记密码</el-button>
    </div>

    <el-input class="input-box" v-model="username" placeholder="用户名 (Email 地址)"></el-input>

    <el-input class="input-box" v-model="password" placeholder="密码" type="password"></el-input>

    <el-button class="login-btn" type="primary" @click="onClickLogin">登录</el-button>
  </div>
</template>

<script>
var sha256 = require("js-sha256").sha256;

export default {
  name: "LoginView",

  data() {
    return {
      username: "test",
      password: "test"
    };
  },

  methods: {
    forgetPassword() {
      this.$router.push("forget-password");
    },

    signUp() {
      this.$router.push("signup");
    },

    onClickLogin() {
      var self = this;

      var on_ok = res => {
        self.onSuccess();
      };

      var on_err = res => {
        self.onError();
      };

      this.requestLogin(on_ok, on_err);
    },

    requestLogin(on_ok, on_error) {
      var secret = this.password;
      var name = this.username;
      var mechanism = "PASSWORD";

      secret = sha256(secret);
      secret = sha256(secret);
      secret = sha256(secret);

      // rest
      var type = "Auth";
      var id = null;
      var data = {
        auth: { mechanism, name, secret }
      };

      this.$store.dispatch("rest/post", { type, id, data, on_ok, on_error });
    },

    onSuccess() {
      this.$router.push("starting");
    },

    onError() {
      var message = "用户名或密码错误。";
      var type = "error";
      var duration = 10000;
      var showClose = true;
      this.$message({ message, type, duration, showClose });
    }
  }
};
</script>
